<template>
    <div>
        <div class="login-underlay" id="login-underlay">
            <Header />
            <Footer />
        </div>
        <div class="login">
            <div class="login-box-bg hidden" id="login-box-bg"></div>
            <div class="login-box-wrapper">
                <div class="login-box smaller" id="login-box">
                    <div class="box-title">Login</div>
                    <div class="box-body">
                        <div class="errormsg" v-html="this.error"></div>
                        <form @submit.prevent="handleFormSubmit">
                            <fieldset>
                                <div class="errormsg hidden">We could not log you in</div>
                                <div class="login-box-row label">
                                    <input type="text" id="username" name="username" ref="userName" autocomplete="off" placeholder="Username or email" v-on:change="checkEmail" />
                                    <div class="username-question" v-tooltip="usernameHelp">
                                        <span class="icon-help-round"></span>
                                    </div>
                                </div>
                                <div class="login-box-row label" v-if="showEmailUsers" v-cloak>
                                    <div class="select">
                                        <select id="emailuser" v-model="emailUser" name="emailuser">
                                            <option value="null" disabled selected>Username and company</option>
                                            <option v-for="user in availableEmailUsers" v-bind:value="user.UserName" :key="user.UserName">{{ user.UserName }}, {{ user.CompanyName }} ({{ user.CompanyNumber }})</option>
                                        </select>
                                    </div>
                                </div>
                                <div class="login-box-row label">
                                    <input type="password" id="password" name="password" ref="password" autocomplete="off" placeholder="Password" />
                                </div>
                                <div class="login-box-row buttons">
                                    <div class="loader" v-show="loading"></div>
                                    <input type="submit" value="Login" class="button green" id="loginButton" v-show="!loading" v-cloak />
                                </div>
                                <div class="login-box-row buttons">
                                    <input type="checkbox" id="rememberMe" name="rememberMe" v-model="rememberMe" />
                                    <label for="rememberme">Remember my username</label>
                                </div>
                                <div class="login-box-row buttons stacked">
                                    <router-link
                                        :to="{
                                            path: '/Recover',
                                            query: {redirectUrl: redirectUrl},
                                        }">
                                        I have forgotten my password
                                    </router-link>
                                </div>
                            </fieldset>
                        </form>
                    </div>
                </div>
            </div>
            <CitrixLogin />
        </div>
    </div>
</template>

<script>
    import CitrixLogin from "@/components/CitrixLogin.vue";
    import Header from "@/components/Header.vue";
    import Footer from "@/components/Footer.vue";

    export default {
        name: "Login",
        props: ["errorMsg"],
        components: {
            CitrixLogin,
            Header,
            Footer,
        },
        data() {
            return {
                usernameHelp: "This can be your username, email, or membership number (but not your surname).",
                error: this.errorMsg || "",
                loading: false,
                availableEmailUsers: null,
                redirectUrl: "",
                emailUser: null,
                title: "",
                rememberMe: false,
            };
        },
        computed: {
            showEmailUsers() {
                return this.availableEmailUsers && this.availableEmailUsers.length > 1;
            },
        },
        methods: {
            populatePage() {
                this.loading = true;
                if (typeof this.$route.query != "undefined" && typeof this.$route.query.redirectUrl != "undefined") {
                    this.redirectUrl = this.$route.query.redirectUrl;
                }
                this.$ajax
                    .post("/auth/PopulatePage", {
                        RedirectUrl: this.$validUrl(this.redirectUrl) ? this.redirectUrl : "",
                    })
                    .then(async (resp) => {
                        if (typeof resp.data != "undefined") {
                            const {Error, ClientRedirect, RedirectUrl: redirectUrl, Questions, EmailUsers, RememberMe, UserName} = resp.data;
                            if (Error) {
                                this.error = Error;
                                if (redirectUrl && ClientRedirect) {
                                    window.location.href = redirectUrl;
                                    return false;
                                }
                            } else if (ClientRedirect && redirectUrl) {
                                window.location.href = redirectUrl;
                                return false;
                            } else if (RedirectPage != null) {
                                var params = {};
                                if (RedirectPage == "SetupQuestions" && Questions.length) {
                                    params = {questions: Questions};
                                }
                                this.$router.push({
                                    name: RedirectPage,
                                    params: params,
                                    query: {
                                        redirectUrl,
                                    },
                                });
                            }

                            if (RememberMe && UserName) {
                                this.$refs.userName.value = UserName;
                                this.rememberMe = true;
                            }

                            if (EmailUsers != null && EmailUsers.Users != null) {
                                this.availableEmailUsers = EmailUsers.Users;
                            }
                        }
                    })
                    .catch(() => {})
                    .then(() => {
                        this.loading = false;
                    });
            },
            checkEmail() {
                let emailInput = this.$refs.userName.value;
                if (emailInput && this.$validateEmail(emailInput)) {
                    this.loading = true;
                    this.$ajax
                        .get("/auth/CheckEmail", {
                            params: {
                                email: emailInput,
                            },
                        })
                        .then((resp) => {
                            if (emailInput == resp.data.EmailAddress) {
                                this.availableEmailUsers = resp.data.Users;
                            }
                        })
                        .catch()
                        .then(() => {
                            this.loading = false;
                        });
                }
            },
            handleFormSubmit() {
                const {userName, password, rememberMe} = this.$refs;
                let data = {
                    redirectUrl: this.$validUrl(this.redirectUrl) ? this.redirectUrl : "",
                    userName: userName.value,
                    password: password.value,
                    rememberMe: this.rememberMe,
                    emailUser: this.emailUser,
                };
                this.error = "";
                if (data.userName == "" && data.password == "") {
                    this.error = "Username and Password cannot be blank.";
                } else if (data.userName == "") {
                    this.error = "Username cannot be blank.";
                } else if (data.password == "") {
                    this.error = "Password cannot be blank.";
                }
                if (this.error != "") {
                    return;
                }

                this.loading = true;

                this.$ajax
                    .post("/auth/login", data)
                    .then(async (resp) => {
                        if (typeof resp.data != "undefined") {
                            const {Error, ClientRedirect, RedirectUrl: redirectUrl, RedirectPage} = resp.data;
                            if (Error != null) {
                                this.error = resp.data.Error;
                            } else {
                                let channels, mfaNotSet;

                                if (["SetupPassword", "Emails"].includes(RedirectPage) < 0 || RedirectPage == null) {
                                    const {authenticated, mfaRequired, otpChannels} = await this.$checkMFACookie(redirectUrl);
                                    mfaNotSet = mfaRequired && !authenticated;
                                    channels = otpChannels;
                                }

                                if (ClientRedirect && redirectUrl) {
                                    if (mfaNotSet) {
                                        this.$router.push({
                                            name: "MFA",
                                            params: {
                                                redirectUrl,
                                                otpChannels: channels,
                                            },
                                        });
                                    } else {
                                        await this.$ajax.post("/legacy/success", null, {withCredentials: true});
                                        window.location.href = redirectUrl;
                                        return false;
                                    }
                                } else if (RedirectPage) {
                                    let params = {
                                        questions: undefined,
                                        otpChannels: channels,
                                        mfaNotSet: mfaNotSet,
                                    };
                                    if (RedirectPage == "SetupQuestions" && Questions.length) {
                                        params.questions = resp.data.Questions;
                                    }
                                    this.$router.push({
                                        name: RedirectPage,
                                        params: params,
                                        query: {
                                            redirectUrl,
                                        },
                                    });
                                }
                            }
                        }
                    })
                    .catch(() => {})
                    .then(() => {
                        this.loading = false;
                    });
            },
        },
        async mounted() {
            this.title = this.$router.history.current.meta.title;
            this.populatePage();
        },
    };
</script>
